import React from "react";

const TextField = ({ sentense, setSentense, setNextIsText, inputEl }) => {
	const handleChange = (e) => {
		setSentense(e.target.value);
		setNextIsText(false);
	};
	return (
		<div className="text-field">
			<input
				type="text"
				value={sentense}
				onChange={handleChange}
				ref={inputEl}
			/>
		</div>
	);
};

export default TextField;
